import isElementInViewPort from "./isElementInViewPort"
import getAllIndexes from "./getAllIndexes"
import median from "./median"

function determineLevelInView(
        lvlHeightRefArray, 
        strandHeightRefArray, 
        illHeightRefArray,
        NUMBER_OF_LEVELS,
        scrollY
    ){
        let levelInView = 1
        let refArray = null
        
        if (scrollY === 0) return NUMBER_OF_LEVELS
        if (scrollY === window.scrollMaxY) return levelInView
        
        if (lvlHeightRefArray[0].current !== null) refArray = lvlHeightRefArray
        if (strandHeightRefArray[0].current !== null) refArray = strandHeightRefArray
        if (illHeightRefArray[0].current !== null) refArray = illHeightRefArray

        if (refArray === null) return levelInView
        console.log(refArray)
        
        let inViewArray = refArray.map(a => isElementInViewPort(a))

        console.log(inViewArray)

        let allIndexes = getAllIndexes(inViewArray, 1)
        levelInView = median(allIndexes)

        return levelInView
}

export default determineLevelInView