import StrandLevelTitle from "./StrandLevelTitle"
import StrandName from '../SharedContentView/StrandName'

function LevelOfStrands(props){

    const width = props.width
    const scrollY = props.scrollY
    const margin = props.margin
    const levelNumber = props.levelNumber
    const strandNumber = props.strandNumber
    const strandName = props.strandName
    const strandLabelWidth = props.strandLabelWidth
    const numberOfLevels = props.numberOfLevels

    let renderTitle 
    strandNumber === 0 ? renderTitle = true : renderTitle = false

    let topOrTail = ''
    let showStrandName = false
    let marginTop = '0vh'
    if (levelNumber === numberOfLevels) {
        topOrTail = 'bt'
        marginTop = '5vh'
        showStrandName = true
    }   
    if (levelNumber === 1) topOrTail = 'bb br2 mb3'

    return (
        <>
            <div 
                className={'flex flex-column bg-white ml2 mr2 bl br bw1 b--dark-purple '+topOrTail} 
                style={{width: `${width}px`, marginTop: `${marginTop}`, boxShadow: '2px 2px 0px 0 rgba(0,0,0,.2)'}}>
                {showStrandName && 
                    <StrandName 
                        scrollY={scrollY}
                        strandName={strandName} 
                        width={strandLabelWidth}/>}
                    <StrandLevelTitle 
                        margin={margin}
                        renderTitle={renderTitle}
                        levelNumber={levelNumber} />
                            {props.children}
            </div>
        </>
    )
}

export default LevelOfStrands