/* BEGIN HANDLE CLICK AND RENDER FUNCTION */
import * as Scroll from 'react-scroll';
import mapEventFromLayerOneToN from "./mapEventFromLayerOneToN"
import mapEventFromLayerMtoN from "./mapEventFromLayerMtoN"

// 4,3,2 to 1 is done

// 1 to 2
// 1 to 3
// 1 to 4

// 2 to 3
// 2 to 4
// 3 to 2
// 3 to 4
// 4 to 2
// 4 to 3

const scroll = Scroll.animateScroll;

function mapClickEventToContentViewAction(
    event,
    NUMBER_OF_LEVELS,
    renderLayerNumber,
    setRenderLayerNumber,
    setNumericalScaleHeight,
    refArray,
    lvlHeightRefArray,
    strandHeightRefArray,
    illHeightRefArray
) {


    const toBottom = 99999999

    let nameValueArray = event.target.attributes.name.nodeValue.split('-')
    let lastControllerClickTemp = {
        Layer: Number(nameValueArray[1]), 
        Level: Number(nameValueArray[3]), 
        Strand: Number(nameValueArray[5])
    }

    // from layer N to layer 1
    if (lastControllerClickTemp.Layer === 1){
        setNumericalScaleHeight(0)
        setRenderLayerNumber(1)
        window.scrollTo(0,0)
      return 
    }

    // from layer 1 to layer N
    if (renderLayerNumber === 1) 
        mapEventFromLayerOneToN(lastControllerClickTemp.Layer, refArray, setRenderLayerNumber, setNumericalScaleHeight)

    // const lvlHeights = lvlHeightRefArray.map(a => a.current.clientHeight)
    // const strandHeights = strandHeightRefArray.map(a => a.current.clientHeight)
    // const illHeights = illHeightRefArray.map(a => a.current.clientHeight) 


    // from layer m to n excluding 1
    if (renderLayerNumber !== 1 && renderLayerNumber !== lastControllerClickTemp.Layer){
        mapEventFromLayerMtoN(
            renderLayerNumber, 
            lastControllerClickTemp.Layer, 
            refArray, 
            setRenderLayerNumber, 
            setNumericalScaleHeight,
            lvlHeightRefArray,
            strandHeightRefArray,
            illHeightRefArray)
            
        return
    }

    // same layer click
    if (renderLayerNumber === lastControllerClickTemp.Layer){
        let clientHeight = refArray[renderLayerNumber].current.clientHeight
        let scaleFactor = ((NUMBER_OF_LEVELS-lastControllerClickTemp.Level)/NUMBER_OF_LEVELS)
        let scrollToValue = scaleFactor*clientHeight
        scroll.scrollTo(scrollToValue, 
            { 
                smooth:true, 
                delay: 100,
                duration: 2000
            })
    }    
}

export default mapClickEventToContentViewAction