function StrandLevelTitle(props){
    
    const levelNumber = props.levelNumber
    const renderTitle = props.renderTitle
    const margin = props.margin
    
    return (
        <div className='bg-white b--dark-purple bw1'> 
            <div 
                className='mt2 mb2 bb b--lpe-blue dark-purple b bw2 f5' 
                style={{
                    marginTop: '20px',
                    marginRight: `${margin}px`,
                    marginLeft: `${margin}px`}}>
                        {renderTitle && <div>Level {levelNumber}</div>}
                        {!renderTitle && <div>&nbsp;</div>}
            </div>
        </div>
    )
}

export default StrandLevelTitle