function mapEventFromLayerOneToN(controllerClickLayerNumber, refArray, setRenderLayerNumber, setNumericalScaleHeight){
    
    const toBottom = 99999999
    
    setTimeout(()=>{
        setRenderLayerNumber(controllerClickLayerNumber)
        setNumericalScaleHeight(refArray[controllerClickLayerNumber].current.clientHeight)
        window.scrollTo(0,toBottom)
    },0)
    
    return
}

export default mapEventFromLayerOneToN